/* Standardized Status Transition Buttons */
.process-order-button,
.process-button,
.deliver-button,
.received-button,
.complete-button,
.office-deliver-button,
.billing-button {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: var(--white);
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 140px;
  text-align: center;
}

.process-order-button::before,
.process-button::before,
.deliver-button::before,
.received-button::before,
.complete-button::before,
.office-deliver-button::before,
.billing-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.6s ease;
}

.process-order-button:hover:not(:disabled)::before,
.process-button:hover:not(:disabled)::before,
.deliver-button:hover:not(:disabled)::before,
.received-button:hover:not(:disabled)::before,
.complete-button:hover:not(:disabled)::before,
.office-deliver-button:hover:not(:disabled)::before,
.billing-button:hover:not(:disabled)::before {
  left: 100%;
}

.process-order-button:hover:not(:disabled),
.process-button:hover:not(:disabled),
.deliver-button:hover:not(:disabled),
.received-button:hover:not(:disabled),
.complete-button:hover:not(:disabled),
.office-deliver-button:hover:not(:disabled),
.billing-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(32, 178, 170, 0.3);
  filter: brightness(1.1);
}

.process-order-button:active:not(:disabled),
.process-button:active:not(:disabled),
.deliver-button:active:not(:disabled),
.received-button:active:not(:disabled),
.complete-button:active:not(:disabled),
.office-deliver-button:active:not(:disabled),
.billing-button:active:not(:disabled) {
  transform: translateY(0);
  box-shadow: 0 4px 15px rgba(32, 178, 170, 0.2);
}

/* Status-specific button colors */
.process-order-button {
  background: linear-gradient(135deg, #e91e63, #c2185b);
}

.process-order-button:hover:not(:disabled) {
  box-shadow: 0 8px 25px rgba(233, 30, 99, 0.3);
}

.process-button {
  background: linear-gradient(135deg, #9c27b0, #7b1fa2);
}

.process-button:hover:not(:disabled) {
