}

/* Disabled states for all status buttons */
.process-order-button.disabled,
.process-button.disabled,
.deliver-button.disabled,
.received-button.disabled,
.complete-button.disabled,
.office-deliver-button.disabled,
.billing-button.disabled,
.process-order-button:disabled,
.process-button:disabled,
.deliver-button:disabled,
.received-button:disabled,
.complete-button:disabled,
.office-deliver-button:disabled,
.billing-button:disabled {
