  color: var(--primary-color);
  margin-bottom: 16px;
  font-weight: 600;
}

.process-info ul {
  margin-left: 20px;
  color: var(--text-primary);
}

/* Welcome Popup */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  backdrop-filter: blur(8px);
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.welcome-popup {
  background: var(--white);
  border-radius: var(--border-radius-large);
  padding: 48px 40px;
  box-shadow: var(--shadow-large);
  text-align: center;
  max-width: 500px;
  width: 90%;
  animation: slideInScale 0.4s ease;
}

@keyframes slideInScale {
  from {
    opacity: 0;
    transform: scale(0.8) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.popup-icon {
  margin-bottom: 24px;
}

.success-icon {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  font-weight: bold;
  margin: 0 auto;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.popup-content h2 {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.popup-content p {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: 24px;
}

.progress-container {
  margin-bottom: 32px;
  text-align: center;
}

.progress-container p {
  margin-bottom: 16px;
  font-size: 16px;
  color: var(--text-secondary);
}

.progress-bar {
  width: 100%;
  height: 8px;
  background-color: var(--border-light);
  border-radius: 4px;
  overflow: hidden;
  margin: 0 auto;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--primary-dark));
  border-radius: 4px;
  transition: width 0.1s linear;
  animation: progressGlow 2s ease-in-out infinite;
}

@keyframes progressGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(32, 178, 170, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(32, 178, 170, 0.8);
  }
}

.popup-close-button {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 12px 32px;
  border-radius: 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.popup-close-button:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
  box-shadow: var(--shadow-medium);
}

/* Custom Multi-Select Dropdown Styles */
.custom-multi-select {
  position: relative;
  width: 100%;
}

.multi-select-trigger {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-light);
  border-radius: var(--border-radius);
  background: var(--white);
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  transition: all 0.2s ease;
  min-height: 44px;
}

.multi-select-trigger:hover {
  border-color: var(--primary-color);
}

.multi-select-trigger:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(32, 178, 170, 0.1);
}

.multi-select-trigger .placeholder {
  color: var(--text-secondary);
}

.multi-select-trigger .selected-text {
  color: var(--text-primary);
}

.dropdown-arrow {
  transition: transform 0.2s ease;
  color: var(--text-secondary);
  font-size: 12px;
}

.dropdown-arrow.open {
  transform: rotate(180deg);
}

.multi-select-dropdown-content {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: var(--white);
  border: 1px solid var(--border-light);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-medium);
  z-index: 1000;
  max-height: calc(5 * 52px + 50px);
  overflow-y: hidden;
  margin-top: 2px;
}

.multi-select-option {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.multi-select-option:last-child {
  border-bottom: none;
}

.multi-select-option:hover {
  background-color: rgba(32, 178, 170, 0.05);
}

.multi-select-option input[type="checkbox"] {
  margin: 0;
  margin-right: 12px;
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.multi-select-option .option-text {
  flex: 1;
  font-size: 14px;
  color: var(--text-primary);
}

/* Custom checkbox styling */
.multi-select-option input[type="checkbox"] {
  appearance: none;
  width: 16px;
  height: 16px;
  border: 2px solid var(--border-light);
  border-radius: 3px;
  position: relative;
  cursor: pointer;
  transition: all 0.2s ease;
}

.multi-select-option input[type="checkbox"]:checked {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.multi-select-option input[type="checkbox"]:checked::after {
  content: '';
  position: absolute;
  top: -2px;
  left: 1px;
  color: white;
  font-size: 12px;
  font-weight: bold;
}

/* Amendment form styles */
.amendment-form {
  background: var(--background-light);
  padding: 20px;
  border-radius: var(--border-radius);
  margin: 20px 0;
  border: 2px solid var(--primary-color);
}

.amendment-form h4 {
