  gap: 16px;
  margin-bottom: 16px;
}

.form-grid .full-width {
  grid-column: 1 / -1;
}

.amendment-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.save-amendment-button {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: var(--border-radius);
