  transition: all 0.2s ease;
}

.procedure-tab:hover {
  border-color: #007bff;
  color: #007bff;
}

.procedure-tab.active {
  background-color: #007bff;
  border-color: #007bff;
  color: white;
  box-shadow: 0 2px 4px rgba(0, 123, 255, 0.2);
}


/* Mute Button Styles */
.mute-button {
  background: transparent;
  border: 2px solid #ddd;
  color: #666;
  padding: 8px 12px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-size: 16px;
  margin-right: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 44px;
  height: 44px;
}

.mute-button:hover {
  border-color: #007bff;
  color: #007bff;
  transform: scale(1.05);
}

.mute-button.sound-on {
  background: #28a745;
  border-color: #28a745;
  color: white;
}

.mute-button.sound-on:hover {
  background: #218838;
  border-color: #218838;
}

.mute-button.sound-off {
  background: #dc3545;
  border-color: #dc3545;
  color: white;
}

.mute-button.sound-off:hover {
  background: #c82333;
  border-color: #c82333;
}

/* Notification Bell Styles */
.notification-bell-container {
  position: relative;
  display: inline-block;
  margin-right: 8px;
}

.notification-bell {
  background: transparent;
  border: 2px solid #ddd;
  color: #666;
  padding: 8px 12px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-size: 18px;
  transition: all 0.2s ease;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 44px;
  height: 44px;
}

.notification-bell:hover {
  border-color: #007bff;
  color: #007bff;
  transform: scale(1.05);
}

.notification-bell.has-unread {
  animation: bellShake 0.8s ease-in-out infinite;
  border-color: #ffc107;
  color: #ffc107;
}

@keyframes bellShake {
  0%, 50%, 100% { transform: rotate(0deg); }
  10%, 30% { transform: rotate(-10deg); }
  20%, 40% { transform: rotate(10deg); }
}

.notification-badge {
  position: absolute;
  top: -8px;
  right: -8px;
  background: #dc3545;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 11px;
  font-weight: bold;
  min-width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid white;
}

.notification-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background: white;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-large);
  width: 350px;
  max-height: 400px;
  z-index: 1000;
  margin-top: 8px;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid #eee;
  background: #f8f9fa;
  border-radius: var(--border-radius) var(--border-radius) 0 0;
}

.notification-header h3 {
  margin: 0;
  font-size: 16px;
  color: #333;
}

.mark-all-read-button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  font-size: 12px;
  padding: 4px 8px;
  border-radius: 4px;
  transition: background 0.2s ease;
}

.mark-all-read-button:hover {
  background: rgba(0, 123, 255, 0.1);
}

.notification-list {
  max-height: 300px;
  overflow-y: auto;
}

.notification-item {
  padding: 12px 16px;
  border-bottom: 1px solid #f0f0f0;
  cursor: pointer;
  transition: background 0.2s ease;
  position: relative;
}

.notification-item:hover {
  background: #f8f9fa;
}

.notification-item.unread {
  background: rgba(0, 123, 255, 0.05);
  border-left: 4px solid #007bff;
}

.notification-item.urgent {
  border-left: 4px solid #dc3545;
  background: rgba(220, 53, 69, 0.05);
}

.notification-content {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.notification-icon {
  font-size: 16px;
  margin-top: 2px;
}

.notification-text {
  flex: 1;
  min-width: 0;
}

.notification-title {
  font-weight: 600;
  font-size: 14px;
  color: #333;
  margin-bottom: 4px;
}

.notification-message {
  font-size: 13px;
  color: #666;
  line-height: 1.4;
  margin-bottom: 4px;
}

.notification-time {
  font-size: 11px;
  color: #999;
}

.notification-close {
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  font-size: 12px;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  margin-left: 8px;
}

.notification-close:hover {
  background: rgba(220, 53, 69, 0.1);
  color: #dc3545;
}

.unread-indicator {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  background: #007bff;
  border-radius: 50%;
}

.no-notifications {
  padding: 40px 20px;
  text-align: center;
  color: #666;
}

.no-notifications-icon {
  font-size: 32px;
  margin-bottom: 12px;
}

.no-notifications p {
  margin: 0 0 8px 0;
  font-weight: 500;
}

.no-notifications small {
  color: #999;
}

.notification-footer {
  padding: 12px 16px;
  text-align: center;
  border-top: 1px solid #eee;
  background: #f8f9fa;
  border-radius: 0 0 var(--border-radius) var(--border-radius);
}

.notification-footer small {
  color: #666;
}

/* Toast Notification Styles */
.toast-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 10000;
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
}

.toast-notification {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-large);
  border: 1px solid #ddd;
  overflow: hidden;
  position: relative;
  transform: translateX(100%);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  max-width: 100%;
  min-width: 300px;
}

.toast-notification.toast-visible {
  transform: translateX(0);
  opacity: 1;
}

.toast-notification.toast-leaving {
  transform: translateX(100%);
  opacity: 0;
  transition: all 0.3s ease-in;
}

.toast-content {
  display: flex;
  align-items: flex-start;
  padding: 16px;
  gap: 12px;
  position: relative;
}

.toast-icon {
  font-size: 20px;
  margin-top: 2px;
  flex-shrink: 0;
}

.toast-text {
  flex: 1;
  min-width: 0;
}

.toast-title {
  font-weight: 600;
  font-size: 14px;
  color: #333;
  margin-bottom: 4px;
  line-height: 1.4;
}

.toast-message {
  font-size: 13px;
  color: #666;
  line-height: 1.4;
}

.toast-action {
  background: none;
  border: 1px solid #007bff;
  color: #007bff;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.2s ease;
  margin-right: 8px;
  flex-shrink: 0;
}

.toast-action:hover {
  background: #007bff;
  color: white;
}

.toast-close {
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  font-size: 12px;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
  flex-shrink: 0;
  align-self: flex-start;
}

.toast-close:hover {
  background: rgba(0, 0, 0, 0.1);
  color: #666;
}

.toast-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  background: #007bff;
  animation: toastProgress linear forwards;
  transform-origin: left;
}

@keyframes toastProgress {
  from { width: 100%; }
  to { width: 0%; }
}

/* Toast type specific styles */
.toast-success {
  border-left: 4px solid #28a745;
}

.toast-success .toast-icon {
  color: #28a745;
}

.toast-error {
  border-left: 4px solid #dc3545;
}

.toast-error .toast-icon {
  color: #dc3545;
}

.toast-warning {
  border-left: 4px solid #ffc107;
}

.toast-warning .toast-icon {
  color: #ffc107;
}

.toast-info {
  border-left: 4px solid #17a2b8;
}

.toast-info .toast-icon {
  color: #17a2b8;
}

/* Responsive design for mobile */
@media (max-width: 480px) {
  .toast-container {
    left: 16px;
    right: 16px;
    top: 16px;
    max-width: none;
  }
  
  .toast-notification {
    min-width: 0;
    max-width: 100%;
  }
  
  .toast-content {
    padding: 12px;
  }
  
  .toast-title {
    font-size: 13px;
  }
  
  .toast-message {
    font-size: 12px;
  }
}

