.process-order-button {
  background-color: #667eea;
  color: white;
}

.process-order-button:hover {
  background-color: #5a6fd8;
}

.amend-button {
  background-color: #2196f3;
  color: white;
}

.amend-button:hover {
  background-color: #1976d2;
}

.delete-button {
  background-color: #f44336;
  color: white;
}

.delete-button:hover {
  background-color: #d32f2f;
